@use '../utils/' as u;

.hero {
    color: hsl(var(--clr-white));
    overflow: hidden;
    position: relative;
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: hsl(var(--clr-primary-dark));
        clip-path: polygon(100% 0, 100% 67%, 69% 92%, 0 100%, 0 0);
        z-index: -1
    }

    .container {
        @include u.container;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 9.5em 0 5em;

        .right {
            display: none;
        }

    }

    @include u.breakpoint-up(md) {
        clip-path: polygon(78% 0, 82% 70%, 69% 92%, 0 100%, 0 0);

        .container {
            justify-content: space-between;
            text-align: left;
        }
        
        .right {
            display: block;    
        }
    }
} 

.intro-desc {
    max-width: 450px;
    margin: 0 auto;
    margin-bottom: 5em ;

    @include u.breakpoint-up(md) {
        margin: unset;
        margin-bottom: 5em ;
    }
}

.learn-more-btn {
    font-family: var(--ff-inter);
    font-size: var(--fs-small);
    font-weight: var(--fw-bold);
    background-color: transparent;
    color: hsl(var(--clr-white));
    border: 1px solid hsl(var(--clr-accent));
    padding: .5em 1.5em;
    cursor: pointer;
    transition: all 400ms ease-in-out;

    &:hover {
        color: hsl(var(--clr-black));
        background-color: hsl(var(--clr-accent));
    }
}

.hero-design {
    max-width: 600px;
    height: auto;
    border: 1px solid black;
    transform: translate(-60%, -20%);

    @include u.breakpoint-down(lg) {
        display: none;
    }
}

