:root {
  --ff-inter: "Inter", sans-serif;
  --fs-h1: 2.25rem;
  --fs-h2: 1.75rem;
  --fs-h3: 1.5rem;
  --fs-h4: 1.25rem;
  --fs-body: 1rem;
  --fs-small: .875rem;
  --fs-xs: .685rem;
  --fw-bold: 700;
  --fw-medium: 500;
  --fw-regular: 400;
}

@media (min-width: 960px) {
  :root {
    --fs-h1: 2.5rem;
    --fs-h2: 1.875rem;
    --fs-h3: 1.625rem;
    --fs-h4: 1.325rem;
    --fs-body: 1.125rem;
    --fs-small: .925rem;
    --fs-xs: .75rem;
  }
}
:root {
  --clr-primary: 189, 73%, 32%;
  --clr-primary-dark: 193, 31%, 20%;
  --clr-secondary: 0, 0%, 95%;
  --clr-secondary-dark: 332, 84%, 29%;
  --clr-accent: 151, 82%, 55%;
  --clr-black: 0, 0%, 0%;
  --clr-white: 0, 0%, 100%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--ff-inter);
  font-size: var(--fs-body);
  font-weight: var(--fw-regular);
  color: hsl(var(--clr-black));
