
@use '../utils/' as u;

.skills {
    padding: 5em 0;

    .container {
        @include u.container;
    }

    @include u.breakpoint-up(sm) {
        .cards-container {
            grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            gap: 1.5em;
        }

        .card {
            width: 200px;
        }
    }
}

.cards-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(165px, 1fr));
    justify-content: center;
    gap: 1em;
}

.card {
    background-color: hsla(var(--clr-primary-dark), 0);
    padding: 2em 1em;
    width: 165px;
    border: 1px solid hsla(var(--clr-black), .1);
    border-radius: .5em;
    color: hsl(var(--clr-black));
    justify-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: -10px 10px 20px -8px rgba(0,0,0,0.75);
    transition: all 200ms ease-in-out;

    &:hover {
        // background: linear-gradient(to right, hsl(var(--clr-primary-dark)) 0%, hsl(var(--clr-primary-dark)) 50%, hsla(var(--clr-primary-dark), .90) 50%, hsla(var(--clr-primary-dark), .90 ) 100%);
        background-color: hsl(var(--clr-primary));
        color: hsl(var(--clr-white));
        transform: scale(1.03);
        box-shadow: -18px 18px 30px -8px rgba(0,0,0,0.75);
    }
}

.brand {
    width: 4em;
    height: 4em;
    color: black;
    background-color: hsl(var(--clr-white));
    margin-bottom: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    
    &__icon {
        font-size: 3em;
    }

    &__name {
        font-size: var(--fs-body);
        font-weight: var(--fw-medium);
    }
    
    &__desc {
        font-size: var(--fs-xs);
        margin-bottom: 0;
    }

    &__html {
        color: #e34f26;
    }

    &__css {
        color: #002561;
    }
    
    &__js {
        color: #f0db4f;
    }

    &__react {
        color: #00d8ff;
    }

    &__sass {
        color: #cc6699;
    }

    &__bootstrap {
        color: #563d7c;
    }

    &__git {
        color: #f34f29;
    }

    &__nodejs {
        color: #44883e;
    }
}


